export default {};

function createWebGL() {
    const canvas = document.createElement('canvas');
    document.body.append(canvas);
    const dpr = Math.min(2, window.devicePixelRatio);
    canvas.width = (window.innerWidth * dpr) | 0;
    canvas.height = (window.innerHeight * dpr) | 0;
    canvas.style.width = window.innerWidth + 'px';
    canvas.style.height = window.innerHeight + 'px';
    const ctx = canvas.getContext('webgl', {
        alpha: false,
        depth: false,
        stencil: false,
        antialias: false,
        preserveDrawingBuffer: false
    });
    if (!ctx) throw new Error();
    return ctx;
}

const buffers: Array<ArrayBuffer> = [];
const bufferViews: Array<Uint8Array> = [];

const objs: Array<Array<{ rnd:number, idx:number, pg:number }>> = [];

class Test {
    readonly info: HTMLDivElement;
    size: number = 0;

    constructor(type: string, x: number, y: number) {
        this.info = document.createElement("div");
        this.info.innerText = type;
        this.info.style.position = "absolute";
        this.info.style.left = `${x}px`;
        this.info.style.top = `${y}px`;
        this.info.style.width = `200px`;
        this.info.style.height = `50px`;
        this.info.style.border = "1px solid black";
        this.info.style.background = "white";
        this.info.onclick = () => {
            if (type == "JS") {
                for (let i = 0; i < 10; ++i) {
                    this.size += 10_000_000;
                    this.info.innerText = `${type} ${(this.size / 1_000_000) | 0} MB`;
                    const a = new ArrayBuffer(10_000_000);
                    buffers.push(a);
                    const v = new Uint8Array(a);
                    for (let j = 0; j < v.length; ++j) {
                        v[j] = j & 0xFF;
                    }
                    bufferViews.push(v);
                }
            }
            else if (type == "OBJ") {
                for (let i = 0; i < 10; ++i) {
                    const arr = [];
                    for (let j = 0; j < 1_000_000; ++j) {
                        arr.push({rnd: Math.random(), idx: j, pg: i});
                    }
                    objs.push(arr);
                    this.size += 1_000_000;
                }
                this.info.innerText = `${type} ${(this.size / 1_000_000) | 0} billions`;
            }
        };
        document.body.appendChild(this.info);
    }
}

