const GL = createWebGL();
GL.pixelStorei(GL.UNPACK_PREMULTIPLY_ALPHA_WEBGL, 1);
const textures: WebGLTexture[] = [];

const vsSource = `
attribute vec2 aVertexPosition;
attribute vec2 aVertexTexCoord;
varying mediump vec2 vTexCoord;
void main() {
  vTexCoord = aVertexTexCoord;
  gl_Position = vec4(aVertexPosition, 0.0, 1.0);
}
`;

const fsSource = `
uniform lowp sampler2D uSampler;
varying mediump vec2 vTexCoord;
void main() {
  lowp vec4 pixelColor = texture2D(uSampler, vTexCoord);
  gl_FragColor = pixelColor;
}
`;

//
// Initialize a shader program, so WebGL knows how to draw our data
//
function initShaderProgram(vsSource: string, fsSource: string) {
    const vertexShader = loadShader(GL.VERTEX_SHADER, vsSource)!;
    const fragmentShader = loadShader(GL.FRAGMENT_SHADER, fsSource)!;

    // Create the shader program

    const shaderProgram = GL.createProgram()!;
    GL.attachShader(shaderProgram, vertexShader);
    GL.attachShader(shaderProgram, fragmentShader);
    GL.linkProgram(shaderProgram);

    // If creating the shader program failed, alert

    if (!GL.getProgramParameter(shaderProgram, GL.LINK_STATUS)) {
        alert('Unable to initialize the shader program: ' + GL.getProgramInfoLog(shaderProgram));
        return null;
    }

    return shaderProgram;
}

//
// creates a shader of the given type, uploads the source and
// compiles it.
//
function loadShader(type: GLenum, source: string) {
    const shader = GL.createShader(type)!;

    // Send the source to the shader object

    GL.shaderSource(shader, source);

    // Compile the shader program

    GL.compileShader(shader);

    // See if it compiled successfully

    if (!GL.getShaderParameter(shader, GL.COMPILE_STATUS)) {
        alert('An error occurred compiling the shaders: ' + GL.getShaderInfoLog(shader));
        GL.deleteShader(shader);
        return null;
    }

    return shader;
}

const shaderProgram = initShaderProgram(vsSource, fsSource)!;
const programInfo = {
    program: shaderProgram,
    attribLocations: {
        vertexPosition: GL.getAttribLocation(shaderProgram, 'aVertexPosition'),
        vertexTexCoord: GL.getAttribLocation(shaderProgram, 'aVertexTexCoord'),
    },
    uniformLocations: {
        uSampler: GL.getUniformLocation(shaderProgram, 'uSampler'),
    },
};

const ib = GL.createBuffer();
GL.bindBuffer(GL.ELEMENT_ARRAY_BUFFER, ib);
GL.bufferData(GL.ELEMENT_ARRAY_BUFFER, new Uint16Array([0, 1, 2, 2, 3, 0]), GL.STATIC_DRAW);

const buffer = GL.createBuffer();
GL.bindBuffer(GL.ARRAY_BUFFER, buffer);
const buffer_data = [-1, -1, 0, 0, 1, -1, 1, 0, 1, 1, 1, 1, -1, 1, 0, 1];

GL.bufferData(GL.ARRAY_BUFFER, new Float32Array(buffer_data), GL.STATIC_DRAW);

function loop(ts: number) {
    const l = 0.9 + 0.1 * Math.random();
    GL.clearColor(l, l, l, 1);
    GL.clear(GL.COLOR_BUFFER_BIT);
    // GL.viewport(0, 0, GL.canvas.width, GL.canvas.height);
    GL.viewport(0, 0, 100, 100);

    for (const texture of textures) {
        GL.bindTexture(GL.TEXTURE_2D, texture);
    }
    let size = 0;
    for (const bv of bufferViews) {
        size += bv.length;
        bv[(Math.random() * bv.length) | 0] = (Math.random() * 255) | 0;
        const a = bv[(Math.random() * bv.length) | 0];
        bv[(Math.random() * bv.length) | 0] = a;
    }
    for(const o of objs) {
        let obj = o[(Math.random() * o.length) | 0];
        obj.idx = (obj.idx + 1) & 0x7FFFFFFF;
    }

    const actions = nextFrameActions;
    nextFrameActions = [];
    for (const action of actions) {
        action();
    }

    requestAnimationFrame(loop);
}

requestAnimationFrame(loop);

const imageSources: string[] = [];
const N = 100;
const format = 'png';
for (let i = 0; i < N; ++i) {
    imageSources.push(`bundle/atlas-${format}/${i}.${format}`);
}

const imageElements: HTMLImageElement[] = [];
const imageBitmaps: ImageBitmap[] = [];
const SHARED_IMG = document.createElement('img');

function loadTexture(src: string) {
    const img = document.createElement('img');
    img.onload = () => {
        console.log('loaded', img.src);
        const texture = GL.createTexture();
        if (texture) {
            GL.bindTexture(GL.TEXTURE_2D, texture);
            GL.texImage2D(GL.TEXTURE_2D, 0, GL.RGBA, GL.RGBA, GL.UNSIGNED_BYTE, img);
            textures.push(texture);
        }
        img.src = '';
    };
    img.src = src;
    return img;
}

function loadTextureSharedImageAsync(src: string) {
    return new Promise((resolve) => {
        const listener = (e: Event) => {
            console.log('loaded', src);
            const texture = GL.createTexture();
            if (texture) {
                GL.bindTexture(GL.TEXTURE_2D, texture);
                GL.texImage2D(GL.TEXTURE_2D, 0, GL.RGBA, GL.RGBA, GL.UNSIGNED_BYTE, SHARED_IMG);
                textures.push(texture);
            }
            // SHARED_IMG.onload = null;
            SHARED_IMG.src = '';
            SHARED_IMG.removeEventListener('load', listener, false);
            resolve();
        };
        SHARED_IMG.addEventListener('load', listener, false);
        SHARED_IMG.src = src;
    });
}

async function loadTextureDecodeAsync(src: string) {
    SHARED_IMG.decoding = 'async';
    SHARED_IMG.src = src;
    await SHARED_IMG.decode();
    console.log('loaded', src);
    const texture = GL.createTexture();
    if (texture) {
        GL.bindTexture(GL.TEXTURE_2D, texture);
        GL.texImage2D(GL.TEXTURE_2D, 0, GL.RGBA, GL.RGBA, GL.UNSIGNED_BYTE, SHARED_IMG);
        textures.push(texture);
    }
    SHARED_IMG.src = '';
}

