function loadImage(src: string): HTMLImageElement {
    const img = document.createElement('img');
    img.onload = () => {
        console.log('loaded', img.src);
    };
    img.src = src;
    return img;
}

function loadImageAsync(src: string): Promise<HTMLImageElement> {
    return new Promise((resolve, reject) => {
        const img = document.createElement('img');
        img.onload = () => {
            console.log('loaded', img.src);
            resolve(img);
        };
        img.src = src;
    });
}


let nextFrameActions: (() => void)[] = [];

function nextFrameAsync(): Promise<void> {
    return new Promise((resolve) => {
        nextFrameActions.push(() => resolve());
    });
}

async function loadImages() {
    console.log('load images');
    for (const src of imageSources) {
        const el = loadImage(src);
        imageElements.push(el);
    }
}

async function loadImageBitmaps() {
    console.log('load image bitmaps');
    for (const src of imageSources) {
        const el = await loadImageAsync(src);
        const bmp = await createImageBitmap(el, {premultiplyAlpha: "premultiply"});
        imageBitmaps.push(bmp);
    }
}


